@use "base/variables" as v;
@use "base/mixins" as m;

.modal {
    position: absolute;
    background: rgb(0 0 0 / 0.8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .formulario {
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 auto; //alinea horizontal
        transform: translateY(-30rem); //posision inicial animacion
        transition-property: all; //animacion
        transition-duration: 500ms;
        transition-timing-function: ease-in-out;
        opacity: 0;
        border-radius: 1rem;

        legend {
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
        }
        input[type="text"] {
            border: 1px solid v.$gris;
            border-radius: 0.5rem;
        }
        .opciones {
            display: flex;
            justify-content: space-between;
        }
        .submit-nueva-tarea {
            @include m.boton(v.$indigo, v.$blanco);
        }
        .cerrar-modal {
            @include m.boton(v.$naranja, v.$blanco);
        }

        &.animar {
            position: relative; // alinea vertical con estas 3 lineas
            top: 50%;
            transform: translateY(-50%); //posision final animacion
            opacity: 1;
        }
        &.cerrar {
            transform: translateY(-200%);
            opacity: 0;
        }
    }
}
